import React, { useEffect, useState } from 'react'
import { Table, Divider, Tag, Button } from 'antd'
import { getDistance } from 'geolib'
import { loadModules } from 'esri-loader'
import $ from 'jquery'
import jsPDF from 'jspdf'
import 'jspdf-autotable'
// import autoTable from 'jspdf-autotable'

const doc = new jsPDF()

const columnsPom = [
  {
    title: 'Pom Name',
    dataIndex: 'name',
    key: 'name',
    span: 10,
    sorter: (a, b) => a.name.localeCompare(b.name),
    sortDirections: ['ascend']
  },
  {
    title: 'Distance (estimation) in Km',
    dataIndex: 'distance',
    key: 'distance',
    span: 10,
    sorter: (a, b) => a.distance - b.distance
  },
  {
    title: "Status",
    dataIndex: 'status',
    key: 'status',
    span: 10,
    render: status => {
      let color = status == "warning" ? 'yellow' : 'red'
      return (
        <Tag color={color} key={status}>
          {status}
        </Tag>
      )
    }
  },
  // {
  //   title: "Action",
  //   dataIndex: 'action',
  //   key: 'action',
  //   span: 10
  // }
];
const columnsConc = [
  {
    title: 'Concession',
    dataIndex: 'name',
    key: 'name',
    span : 10
  },
  {
    title: 'Distance in Km (Estimation)',
    dataIndex: 'distance',
    key: 'distance',
    span : 10,
    sorter: (a, b) => a.distance - b.distance,
  },
  {
    title: "Status",
    dataIndex: 'status',
    key: 'status',
    span: 10,
    render: status => {
      let color = status == "warning" ? 'yellow' : 'red'
      return (
        <Tag color={color} key={status}>
          {status}
        </Tag>
      )
    }
  },
  // {
  //   title: "Action",
  //   dataIndex: 'action',
  //   key: 'action',
  //   span: 10
  // }
];
const columnsPeat = [
  {
    title : 'Remarks',
    dataIndex : 'remark',
    key : 'remark',
    span : 10
  },
  {
    title: 'Distance in Km (Estimation)',
    dataIndex: 'distance',
    key: 'distance',
    span : 10,
    sorter: (a, b) => a.distance - b.distance,
  },
  {
    title : 'Total Area',
    dataIndex : 'area',
    key : 'area',
    span : 10
  },
  // {
  //   title: "Action",
  //   dataIndex: 'action',
  //   key: 'action',
  //   span: 10
  // }
]
const columnHotspot = [
  {
    title : 'Latitude',
    dataIndex : 'latitude',
    key : 'latitude',
    span : 10
  },
  {
    title : 'Longitude',
    dataIndex : 'longitude',
    key : 'longitude',
    span : 10
  },
  {
    title : 'Acquisition Date',
    dataIndex : 'acq_date',
    key : 'acq_date',
    span : 10
  },
  {
    title : 'Verification',
    dataIndex : 'verification',
    key : 'verification',
    span : 10
  },
]


class ModalContent extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      pomData : [],
      concData : [],
      peatData : [],
      hotspotData : []
    }
  }

  generatePdf = () => {
    // var divContents = $("#dvContainer").html();
    // var printWindow = window.open('', '', 'height=400,width=800');
    // printWindow.document.write('<html><head><title>DIV Contents</title>');
    // printWindow.document.write('</head><body >');
    // printWindow.document.write("tes");
    // printWindow.document.write('</body></html>');
    // printWindow.document.close();
    // printWindow.print();
    // doc.autoTable({
    //   head: [['Name', 'Email', 'Country']],
    //   body: [
    //     ['David', 'david@example.com', 'Sweden'],
    //     ['Castille', 'castille@example.com', 'Spain'],
    //     // ...
    //   ],
    // })
    let headPom = columnsPom.map(el => el.title)
    let dataPom = this.state.pomData.map(
      el => {
        return [el.name, el.distance, el.status]
      }
    )
    doc.autoTable({
      head : [headPom],
      body : dataPom
    })

    let headConc = columnsConc.map(el => el.title)
    let dataConc = this.state.concData.map(
      el => {
        return [el.name, el.distance, el.status]
      }
    )
    doc.autoTable({
      head : [headConc],
      body : dataConc
    })

    let headPeat = columnsPeat.map(el => el.title )
    let dataPeat = this.state.peatData.map(
      el => {
        return [ el.remark, el.distance, el.area ]
      }
    )
    doc.autoTable({
      head : [headPeat],
      body : dataPeat
    })

    let headHs = columnHotspot.map(el => el.title)
    let dataHs = this.state.hotspotData.map(
      el => {
        return [el.latitude, el.longitude, el.acq_date, el.verification]
      }
    )
    doc.autoTable({
      head : [headHs],
      body : dataHs
    })

    doc.save('table.pdf')
  }

  processingData = () => {
    loadModules([
      "esri/geometry/geometryEngine",
      "esri/geometry/projection",
      "esri/geometry/SpatialReference"
    ]).then(([geometryEngine, projection, SpatialReference]) => {
      let props = this.props
      const [arrayPom, arrayConc, arrayPeat, arrayHotspot] = props.data
      console.log("modalcontent", props.data)
      const dfrsLoc = { latitude: props.location.latitude, longitude: props.location.longitude }
      // console.log("hs", arrayHotspot)
      // console.log(arr)
      // destructuring data POM dan loop, sehingga didapatkan jarak ke titik deforestasi
      let dataPom = arrayPom.map((el, i) => {
        let endLoc = { latitude: el.geometry.latitude, longitude: el.geometry.longitude }
        let jarak = getDistance(dfrsLoc, endLoc)
        let status = jarak > 1000 ? 'warning' : 'impacted'
        return {
          key: i,
          no: i + 1,
          name: el.attributes.PomName,
          distance: jarak / 1000,
          distanceNumber: jarak,
          status: status
        }
      })
      // sort ascending
      dataPom.sort((a, b) => a.distanceNumber - b.distanceNumber)
      // console.log("datapom", dataPom)

      let dataHotspot = arrayHotspot.map( (el,i) => {
        return{
          key : i,
          acq_date : el.attributes.Loc_Date,
          latitude : el.attributes.Latitude,
          longitude : el.attributes.Longitude,
          verification : el.attributes.Verificati
        }
      })
      console.log("dataHotspot", dataHotspot)

      // Destructuring data konsesi, karena datanya berupa area dan projeksinya berbeda, perlu load module projection dari Arcmap
      // dan akan mereturn promise
      let dataConc = arrayConc.map((el, i) => {
        return projection.load().then(
          () => {
            let outSpatialReference = new SpatialReference({
              wkid: 3857 //Sphere_Sinusoidal projection
            });
            let g1 = projection.project(props.dfrsArea, outSpatialReference)
            let g2 = projection.project(el.geometry, outSpatialReference)
            let j = geometryEngine.distance(g1, g2)
            let status = j < 1000 ? 'impacted' : 'warning'
            let result = {
              key: i,
              no: i + 1,
              name: el.attributes.Name,
              area: 10,
              distance: (j / 1000).toFixed(2),
              status: status
            }
            return result
          }
        )
      })

      // Destructuring data Peat
      let dataPeat = arrayPeat.map( (el, i) => {
        console.log("el", el)
        return projection.load().then(
          () => {
            let outSpatialReference = new SpatialReference({
              wkid: 3857 //Sphere_Sinusoidal projection
            });
            let g1 = projection.project(props.dfrsArea, outSpatialReference)
            let g2 = projection.project(el.geometry, outSpatialReference)
            let j = geometryEngine.distance(g1, g2)
            let status = j < 1000 ? 'impacted' : 'warning'
            return {
              remark : el.attributes.Category,
              area : el.attributes["SHAPE.STArea()"],
              key : i,
              status : status,
              distance: (j / 1000).toFixed(2),
            }
          }
        )
      })

      // Karena data konsesi yang bersinggunan mereturn promise, makan perlu di promise all array tersebut, setekah promise sukses kemudian ubah state agar tampilan data berubah
      Promise.all(dataConc).then(
        // konsesi => setConcData(konsesi)
        konsesi => this.setState({
          concData : konsesi
        })
      )
      Promise.all(dataPeat).then(
        // peat => setPeatData(peat)
        peat => this.setState({
          peatData : peat
        })
      )
      // setPomData(dataPom)
      // setHotspotData(dataHotspot)

      this.setState({
        hotspotData : dataHotspot,
        pomData : dataPom
      })
    })
  
  }

  componentDidMount(){
    this.processingData()
  }

  // componentDidUpdate(){
  //   this.processingData()
  // }

  render(){
    let { pomData, concData, peatData, hotspotData} = this.state
    return(
    <div style={{ height: '60vh', overflow: 'scroll', paddingLeft: '5vw', paddingRight: '5vw' }}>
      <h2>POM</h2>
      <Table id="pomTable" dataSource={pomData} columns={columnsPom} />
      <Divider />
      <h2>Concession</h2>
      <Table dataSource={concData} columns={columnsConc} />
      <Divider />
      <h2>Peat</h2>
      <Table dataSource={peatData} columns={columnsPeat} />
      <Divider />
      <h2>Hotspot</h2>
      <Table dataSource={hotspotData} columns={columnHotspot} />
      <Divider />
      <Button type="dashed" onClick={this.generatePdf} >Export to PDF</Button>
      <Divider />
    </div>
    )
  }

}


export default ModalContent